
import Vue from "vue";
import { Component, Prop } from "vue-property-decorator";
import { DesignProvider, DesignLibraryData } from "@/types/design-library";
import NotificationLogger from "@/lib/notifications/notifications";
import { pipe } from "fp-ts/function";
import { makeWordPressAjaxRequest } from "@/lib/wordpress/ajax";
import * as t from "io-ts";
import { fold } from "fp-ts/Either";
import DesignLibraryStatusIcon from "@/subapps/design-library/DesignLibraryStatusIcon.vue";
import {
  DataStatuses,
  RequestStatus,
  TemplateIdsToImport,
  TemplateIdsToImportV
} from "@/subapps/design-library/types";
import { PropertiesDataV } from "@/types/tree";
import { regenerateGlobalSettingsCache } from "@/lib/wordpress/regenerate-posts-cache";
import { centerDialogInScreen } from "@/subapps/manage-templates/lib/modal-position";

@Component({
  components: { DesignLibraryStatusIcon }
})
export default class DesignLibraryImportingDialog extends Vue {
  @Prop()
  value!: false | DesignProvider;

  @Prop()
  providerData?: DesignLibraryData;

  importingDataStatus: DataStatuses = {
    trashingTemplates: null,
    globalSettings: null,
    designPresets: null,
    postsAndPages: null,
    templates: null,
    headers: null,
    footers: null,
    popups: null,
    blocks: null,
    variables: null,
    oxySelectors: null
  };

  // makes it simple to store error/success states while making requests
  temporaryDataStatus: DataStatuses = {
    trashingTemplates: null,
    globalSettings: null,
    designPresets: null,
    postsAndPages: null,
    templates: null,
    headers: null,
    footers: null,
    popups: null,
    blocks: null,
    variables: null,
    oxySelectors: null
  };

  remoteHomepageId: null | number = null;
  importedHomepageIdOrFirstPostId: null | number = null;
  idsOfPostsAndPagesToImport: null | number[] = null;

  idsOfTemplatesToImport: TemplateIdsToImport | null = null;
  oldToNewBlockIdsMap: Record<number, number> = {};

  async mounted() {
    if (!this.value) {
      NotificationLogger.log.errorMessage(
        "Couldn't import site, something went wrong"
      );
      return;
    }

    centerDialogInScreen();

    this.applyStatusToAllTemplateStatuses("loading");
    this.importingDataStatus.postsAndPages = "loading";
    this.importingDataStatus.globalSettings = "loading";
    this.importingDataStatus.designPresets = "loading";
    this.importingDataStatus.variables = "loading";
    this.importingDataStatus.oxySelectors = "loading";
    this.importingDataStatus.trashingTemplates = "loading";

    this.setFullSiteLastImportedFromUrl();

    this.idsOfTemplatesToImport = await this.getTemplateIdsToImport(
      this.value.url
    );

    if (this.isGlobalSettingsEnabled) {
      this.importGlobalSettings();
    } else {
      this.importingDataStatus.globalSettings = "successNothingToDo";
    }

    if (this.isDesignPresetsEnabled) {
      this.importDesignPresets();
    } else {
      this.importingDataStatus.designPresets = "successNothingToDo";
    }

    if (this.isVariablesEnabled) {
      this.importVariables();
    } else {
      this.importingDataStatus.variables = "successNothingToDo";
    }

    if (this.isOxySelectorsEnabled) {
      this.importOxySelectors();
    } else {
      this.importingDataStatus.oxySelectors = "successNothingToDo";
    }

    // do it before importing
    await this.trashAllTemplatesHeadersFooters();

    // do it before importing templates, so we can map old ids to new ids
    await this.importAllBlocks();

    this.importAllTemplates();

    // we need to verify the homepage id against imported pages, so await it
    await this.getRemoteHomepageId();

    this.importPostsAndPages();
  }

  get infoToImport(): { label: string; statusKey: keyof DataStatuses }[] {
    const baseItems: { label: string; statusKey: keyof DataStatuses }[] = [
      {
        label: "Posts and Pages",
        statusKey: "postsAndPages"
      },
      {
        label: "Templates",
        statusKey: "templates"
      },
      {
        label: "Headers",
        statusKey: "headers"
      },
      {
        label: "Footers",
        statusKey: "footers"
      },
      {
        label: "Popups",
        statusKey: "popups"
      },
      {
        label: this.$bdox("global_blocks"),
        statusKey: "blocks"
      }
    ];

    const items: { label: string; statusKey: keyof DataStatuses }[] = [];

    if (this.isGlobalSettingsEnabled) {
      items.push({
        label: "Global Settings",
        statusKey: "globalSettings" as keyof DataStatuses
      });
    }

    if (this.isDesignPresetsEnabled) {
      items.push({
        label: "Design Presets",
        statusKey: "designPresets" as keyof DataStatuses
      });
    }

    if (this.isVariablesEnabled) {
      items.push({
        label: "Variables",
        statusKey: "variables" as keyof DataStatuses
      });
    }

    if (this.isOxySelectorsEnabled) {
      items.push({
        label: "Selectors",
        statusKey: "oxySelectors" as keyof DataStatuses
      });
    }

    return [...items, ...baseItems];
  }

  get isGlobalSettingsEnabled() {
    return this.providerData?.isGlobalSettingsEnabled;
  }

  get isDesignPresetsEnabled() {
    return this.providerData?.isDesignPresetsEnabled;
  }

  get isVariablesEnabled() {
    return this.providerData?.isVariablesEnabled;
  }

  get isOxySelectorsEnabled() {
    return this.providerData?.isOxySelectorsEnabled;
  }

  get isPopupsEnabled() {
    return this.providerData?.isPopupsEnabled;
  }

  get canCloseImportingDialog() {
    const isDone = (status: RequestStatus) => status !== "loading";

    return (
      isDone(this.importingDataStatus.postsAndPages) &&
      isDone(this.importingDataStatus.globalSettings) &&
      isDone(this.importingDataStatus.designPresets) &&
      isDone(this.importingDataStatus.templates) &&
      isDone(this.importingDataStatus.blocks) &&
      isDone(this.importingDataStatus.popups) &&
      isDone(this.importingDataStatus.footers) &&
      isDone(this.importingDataStatus.headers) &&
      isDone(this.importingDataStatus.variables) &&
      isDone(this.importingDataStatus.oxySelectors)
    );
  }

  get getImportedSiteHomepageUrl() {
    if (
      !this.value ||
      !this.importedHomepageIdOrFirstPostId ||
      !window.Breakdance.homeUrl
    ) {
      return null;
    }

    const updatedUrl = new URL(window.Breakdance.homeUrl);

    updatedUrl.searchParams.append(window.Breakdance.builderMode, "builder");
    updatedUrl.searchParams.append(
      "id",
      `${this.importedHomepageIdOrFirstPostId}`
    );

    return updatedUrl.href;
  }

  close() {
    this.$emit("close");
  }

  setFullSiteLastImportedFromUrl() {
    if (!this.value) return;

    makeWordPressAjaxRequest({
      action: "breakdance_save_arbitrary",
      dataToPost: {
        key: "design_library_full_site_last_imported_from_url",
        data: this.value.url
      },
      decoder: t.unknown
    });
  }

  async importGlobalSettings() {
    if (!this.value) return;

    pipe(
      await makeWordPressAjaxRequest({
        action: "breakdance_import_external_global_settings",
        decoder: PropertiesDataV,
        dataToPost: {
          url: this.value.url
        },
        friendlyName: "Importing Global Settings"
      }),
      fold(
        () => {
          this.importingDataStatus.globalSettings = "error";
        },
        () => {
          regenerateGlobalSettingsCache();
          this.importingDataStatus.globalSettings = "success";
        }
      )
    );
  }

  async importDesignPresets() {
    if (!this.value) return;

    pipe(
      await makeWordPressAjaxRequest({
        action: "breakdance_import_external_design_presets",
        decoder: t.type({
          success: t.string,
          successNothingToDo: t.union([t.undefined, t.boolean])
        }),
        dataToPost: {
          url: this.value.url
        },
        friendlyName: "Importing Design Presets"
      }),
      fold(
        () => {
          this.importingDataStatus.designPresets = "error";
        },
        response => {
          if (response?.successNothingToDo) {
            this.importingDataStatus.designPresets = "successNothingToDo";
          } else {
            this.importingDataStatus.designPresets = "success";
          }
        }
      )
    );
  }

  async importVariables() {
    if (!this.value) return;

    pipe(
      await makeWordPressAjaxRequest({
        action: "breakdance_import_external_variables",
        decoder: t.type({
          success: t.string,
          successNothingToDo: t.union([t.undefined, t.boolean])
        }),
        dataToPost: {
          url: this.value.url
        },
        friendlyName: "Importing Variables"
      }),
      fold(
        () => {
          this.importingDataStatus.variables = "error";
        },
        response => {
          if (response?.successNothingToDo) {
            this.importingDataStatus.variables = "successNothingToDo";
          } else {
            this.importingDataStatus.variables = "success";
          }
        }
      )
    );
  }

  async importOxySelectors() {
    if (!this.value) return;

    pipe(
      await makeWordPressAjaxRequest({
        action: "breakdance_import_external_oxy_selectors",
        decoder: t.type({
          success: t.string,
          successNothingToDo: t.union([t.undefined, t.boolean])
        }),
        dataToPost: {
          url: this.value.url
        },
        friendlyName: "Importing Oxy Selectors"
      }),
      fold(
        () => {
          this.importingDataStatus.oxySelectors = "error";
        },
        response => {
          if (response?.successNothingToDo) {
            this.importingDataStatus.oxySelectors = "successNothingToDo";
          } else {
            this.importingDataStatus.oxySelectors = "success";
          }
        }
      )
    );
  }

  async importPostsAndPages() {
    if (!this.value) return;

    this.idsOfPostsAndPagesToImport = await this.getPostsIdsToImport(
      this.value.url
    );

    if (this.idsOfPostsAndPagesToImport === null) {
      this.importingDataStatus.postsAndPages = "error";

      return;
    }

    if (!this.idsOfPostsAndPagesToImport.length) {
      this.importingDataStatus.postsAndPages = "successNothingToDo";
      return;
    }

    this.importPostsAndPagesOneByOne(this.idsOfPostsAndPagesToImport);
  }

  async getPostsIdsToImport(url: string) {
    return pipe(
      await makeWordPressAjaxRequest({
        action: "breakdance_design_lib_get_ids_of_posts_and_pages_to_import",
        decoder: t.union([t.array(t.number), t.null]),
        dataToPost: {
          url: url
        },
        friendlyName: "Getting Posts IDs to import"
      }),
      fold(
        () => null,
        idsOfPostsToImport => idsOfPostsToImport
      )
    );
  }

  async importPostsAndPagesOneByOne(idsOfPostsAndPagesToImport: number[]) {
    if (!this.value) return;

    let anyPostFailed = false;
    let anyPostSucceeded = false;

    let firstPostId: number | null = null;

    for (const id of idsOfPostsAndPagesToImport) {
      pipe(
        await makeWordPressAjaxRequest({
          action: "breakdance_design_lib_import_post_or_page",
          decoder: t.type({
            success: t.string,
            postId: t.union([t.number, t.undefined])
          }),
          dataToPost: {
            id,
            url: this.value.url,
            idsToMap: this.oldToNewBlockIdsMap
          },
          friendlyName: "Importing all post and pages"
        }),
        fold(
          () => {
            anyPostFailed = true;
          },
          response => {
            if (!firstPostId && response.postId) {
              firstPostId = response.postId;
            }

            if (id === this.remoteHomepageId && response.postId) {
              this.importedHomepageIdOrFirstPostId = response.postId;
            }

            anyPostSucceeded = true;
          }
        )
      );
    }

    // in case there's no homepage, maybe it's a posts page, or it's not breakdance.
    if (!this.importedHomepageIdOrFirstPostId) {
      this.importedHomepageIdOrFirstPostId = firstPostId;
    }

    this.updateTemporaryState("postsAndPages", anyPostFailed, anyPostSucceeded);
    this.importingDataStatus.postsAndPages =
      this.temporaryDataStatus.postsAndPages;
  }

  updateTemporaryState(
    statusName: keyof DataStatuses,
    anyPostFailed: boolean,
    anyPostSucceeded: boolean
  ) {
    if (anyPostFailed && anyPostSucceeded) {
      this.temporaryDataStatus[statusName] = "partialError";
    } else if (anyPostFailed) {
      this.temporaryDataStatus[statusName] = "error";
    } else if (anyPostSucceeded) {
      this.temporaryDataStatus[statusName] = "success";
    }
  }

  async trashAllTemplatesHeadersFooters() {
    // run them in parallel
    const result = await Promise.all([
      this.trashTemplateByPostType("headers"),
      this.trashTemplateByPostType("footers"),
      this.trashTemplateByPostType("templates"),
      this.trashTemplateByPostType("global_blocks"),
      this.trashTemplateByPostType("popups")
    ]);

    this.importingDataStatus["trashingTemplates"] =
      this.temporaryDataStatus["trashingTemplates"];

    return result;
  }

  async trashTemplateByPostType(
    $templateType:
      | "headers"
      | "footers"
      | "templates"
      | "global_blocks"
      | "popups"
  ) {
    return pipe(
      await makeWordPressAjaxRequest({
        action: "breakdance_design_lib_trash_all_templates_by_type",
        decoder: t.type({ success: t.string }),
        dataToPost: {
          templateType: $templateType
        },
        friendlyName: `Trashing all ${$templateType}`
      }),
      fold(
        () => {
          this.updateTemporaryState(
            "trashingTemplates",
            true,
            this.temporaryDataStatus["trashingTemplates"] === "success"
          );
        },
        () => {
          this.updateTemporaryState(
            "trashingTemplates",
            this.temporaryDataStatus["trashingTemplates"] === "error",
            true
          );
        }
      )
    );
  }

  async importAllTemplates() {
    if (!this.value) return;
    if (!this.idsOfTemplatesToImport) return;

    // no ids, so nothing to import
    if (Object.values(this.idsOfTemplatesToImport).flat().length === 0) {
      this.applyStatusToAllTemplateStatuses("successNothingToDo");
      return;
    }

    // not awaiting these to make them run in parallel
    this.importTemplates(this.idsOfTemplatesToImport.templateIds);
    this.importHeaders(this.idsOfTemplatesToImport.headerIds);
    this.importFooter(this.idsOfTemplatesToImport.footerIds);

    if (this.isPopupsEnabled) {
      this.importPopups(this.idsOfTemplatesToImport.popupIds);
    } else {
      this.importingDataStatus.popups = "successNothingToDo";
    }
  }

  async importAllBlocks() {
    if (!this.value) return;
    if (!this.idsOfTemplatesToImport) return;

    await this.importBlocks(this.idsOfTemplatesToImport.blockIds);
  }

  applyStatusToAllTemplateStatuses(status: RequestStatus) {
    this.importingDataStatus.templates = status;
    this.importingDataStatus.headers = status;
    this.importingDataStatus.footers = status;
    this.importingDataStatus.blocks = status;
    this.importingDataStatus.popups = status;
  }

  async getTemplateIdsToImport(url: string) {
    return pipe(
      await makeWordPressAjaxRequest({
        action: "breakdance_design_lib_get_ids_of_templates_to_import",
        decoder: TemplateIdsToImportV,
        dataToPost: {
          url: url
        },
        friendlyName: "Getting template IDs to import"
      }),
      fold(
        () => {
          this.applyStatusToAllTemplateStatuses("error");

          return null;
        },
        idsOfTemplates => idsOfTemplates
      )
    );
  }

  async importTemplates(idsToImport: number[]) {
    if (!idsToImport.length) {
      this.importingDataStatus.templates = "successNothingToDo";
      return;
    }

    for (const id of idsToImport) {
      await this.importTemplateById(id, "templates");
    }

    this.importingDataStatus.templates = this.temporaryDataStatus.templates;
    this.temporaryDataStatus.templates = null;
  }

  async importHeaders(idsToImport: number[]) {
    if (!idsToImport.length) {
      this.importingDataStatus.headers = "successNothingToDo";
      return;
    }

    for (const id of idsToImport) {
      await this.importTemplateById(id, "headers");
    }

    this.importingDataStatus.headers = this.temporaryDataStatus.headers;
    this.temporaryDataStatus.headers = null;
  }

  async importFooter(idsToImport: number[]) {
    if (!idsToImport.length) {
      this.importingDataStatus.footers = "successNothingToDo";
      return;
    }

    for (const id of idsToImport) {
      await this.importTemplateById(id, "footers");
    }

    this.importingDataStatus.footers = this.temporaryDataStatus.footers;
    this.temporaryDataStatus.footers = null;
  }

  async importPopups(idsToImport: number[]) {
    if (!idsToImport.length) {
      this.importingDataStatus.popups = "successNothingToDo";
      return;
    }

    for (const id of idsToImport) {
      await this.importTemplateById(id, "popups");
    }

    this.importingDataStatus.popups = this.temporaryDataStatus.popups;
    this.temporaryDataStatus.popups = null;
  }

  async importBlocks(idsToImport: number[]) {
    if (!idsToImport.length) {
      this.importingDataStatus.blocks = "successNothingToDo";
      return;
    }

    for (const id of idsToImport) {
      const newId = await this.importTemplateById(id, "blocks");

      if (newId) {
        this.oldToNewBlockIdsMap[id] = newId;
      }
    }

    this.importingDataStatus.blocks = this.temporaryDataStatus.blocks;
    this.temporaryDataStatus.blocks = null;
  }

  async importTemplateById(id: number, stateName: keyof DataStatuses) {
    if (!this.value) return;

    return pipe(
      await makeWordPressAjaxRequest({
        action: "breakdance_design_lib_import_template_by_id",
        decoder: t.type({ success: t.string, postId: t.number }),
        dataToPost: {
          id,
          url: this.value.url,
          idsToMap: this.oldToNewBlockIdsMap
        },
        friendlyName: `Importing template with ID "${id}"`
      }),
      fold(
        () => {
          this.updateTemporaryState(
            stateName,
            true,
            this.temporaryDataStatus[stateName] === "success"
          );

          return null;
        },
        response => {
          this.updateTemporaryState(
            stateName,
            this.temporaryDataStatus[stateName] === "error",
            true
          );

          return response.postId;
        }
      )
    );
  }

  async getRemoteHomepageId() {
    if (!this.value) return;

    return pipe(
      await makeWordPressAjaxRequest({
        action: "breakdance_design_lib_get_homepage_id_to_import",
        decoder: t.type({ id: t.number }),
        dataToPost: {
          url: this.value.url
        },
        friendlyName: `Importing homepage id`
      }),
      fold(
        () => {},
        res => {
          this.remoteHomepageId = res.id;
        }
      )
    );
  }
}
